import * as React from 'react'
import Img from "../defaults/image"
import PortfolioImage from '../portfolio/pImages';
import { Player } from 'video-react';
import "../../style/video.css"

const Card = (props) =>
{
    const { image, title, text, width, heading, headText, type, name } = props.data;
    const { page } = props

    return (
        <>

            <div className={heading ? "card-main flexed" : "card-main"}>
                {
                    heading &&
                    <div className="wText" style={{ width: "37vw", marginRight: "10%" }}>
                        <h1>{heading}</h1>
                        <span>{headText}</span>
                    </div>
                }
                <div>
                    <div className="slider-images" style={{ maxWidth: `${width}px`, width: "40vw" }}>
                        {
                            type === "video" && page === "portfolio" ?
                                <Player
                                    autoPlay
                                    muted
                                    volume={false}
                                    duration={false}
                                >
                                    <source src={`${require(`../video/${name}.mp4`)}`} type="video/mp4" />
                                </Player> : page === "portfolio" ? <PortfolioImage image={image} /> : <Img image={image} />
                        }
                    </div>
                    <div className="card-text">
                        <h3>{title}</h3>
                        <span className="text-span">{text}</span>
                    </div>
                </div>

            </div>
        </>
    );
}

export default Card;
